import styled from 'styled-components';

export default styled.header`
  height: 12vh;
  min-height: 80px;
  max-height: 200px;
  font-style: italic;
  grid-area: header;
  font-size: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
`;



